.header {
  position: relative;
  padding: 10px 15px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ant-badge {
  padding-bottom: 10px;
}

.ant-badge-count {
  background: #cc0000;
  font-size: 14px;
}

.ant-badge:hover + .modal-cart, .modal-cart:hover {
  visibility: visible;
  opacity: 1;
  transform: translate(0, 0);
}


.modal-cart {
  z-index: 100;
  position: absolute;
  top: 105px;
  right: 0;
  padding: 20px;
  border: 4px solid #314a2c;
  background: #fff;
  transform: translate(0, 20px);
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s;
}

.ant-list-item-meta {
  align-items: center;
}

.ant-list-item-meta-title {
  margin: 0;
}

.modal-cart__item {
  font-size: 18px;
}

.ant-list-item-meta-title > a:hover {
  color: #66cdaa
}


.modal-cart .fas {
  color: rgb(101, 67, 33);
}

.modal-cart__count {
  display: inline-block;
  width: 60px;
  text-align: center;
}

.modal-cart__btn {
  margin: 0 10px;
  border-radius: 50%;
  border: 1px solid #314a2c
}

.ant-list-split .ant-list-item {
  font-size: 17px;
}

/* .modal-cart .ant-list-item {
 
} */

.modal-cart .ant-list-item-meta {
  margin-right: 50px;
}

.modal-cart__badge {
  padding-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
  border-top: 1px solid gray;
}

.modal-cart__badge-link {
  margin-right: 20px;
  color: rgba(1, 8, 15, 0.663);
  font-size: 17px;
}

.modal-cart__badge-link:hover {
  color: #66cdaa
}


.modal-cart__badge-total {
  margin-right: 45px;
}